//
// Component animations
// --------------------------------------------------


.fade {
  @include opacity(0);
  @include transition(opacity .15s linear);
  &.in {
    @include opacity(100);
  }
}

.collapse {
  position: relative;
  height: 0;
  overflow: hidden;
  overflow: visible \9;
  @include transition(height .35s ease);
  &.in {
    height: auto;
  }
}



